<script>
import Snow from './snow'
  export default {
    name: 'Announcement',
    components:{
      Snow
    },
    data: () => ({
      photopage: 0,
      width: window.innerWidth,
      height: window.innerHeight,
      imgList:[
        {src:"DSC03976.jpg"},
        {src:"DSC04195.jpg"},
        {src:"DSC04231.jpg"},
        {src:"DSC04378.jpg"},
        {src:"DSC04059.jpg"},
        {src:"DSC04656.jpg"},
      ],
      overlay:false,
      overlayImg: null,
      dday:"",
    }),
    created:function(){
      window.addEventListener('resize', () =>{
            this.height = window.innerHeight
            this.width = window.innerWidth
        })
        this.getDayGap()
    },
    methods:{
      getThomUrl(src){
        return require('../assets/img/thom/'+src)
      },
      getImgUrl(src){
        return require('../assets/img/'+src)
      },
      openOverlay(img){
        this.overlay =true
        this.overlayImg = img
      },
      hideOverlay(){
        this.overlay = false
        this.overlayImg = null
      },
            getDayGap(){

      var dday = new Date("December 11, 2020 00:00:00").getTime();//디데이
      var nowday = new Date();//현재
      nowday = nowday.getTime();//밀리세컨드 단위변환
      var distance = dday - nowday;//디데이에서 현재까지 뺀다.

      var d = Math.floor(distance / (1000 * 60 * 60 * 24));//일
      console.log(d)
      d=d+1
      if (d == 0) {//당일넘어섰을때, dday로 변경
          this.dday = "오늘은 우리 결혼하는 날!"
      } else if(d<0){
        this.dday = "우리 결혼한지 벌써 "+(-1*d) +"일째!"
      } else {
          this.dday= "결혼식날 까지 앞으로 "+d+"일!";
      }
      }
    }
  }
</script>
